import {mapMutations} from 'vuex';
import ajax from '../../utils/ajax.js'
export default {
	data() {
		return {
			good:{}
		}
	},
	// onLoad(options){},
	async mounted(){
		let {goodId} =  this.$mp.query;
		// console.log('options',this.$mp.query.goodId);
		let good = await ajax('/getGoodDetail',{
			goodId
		})
		this.good=good;
		// console.log('good',good)
	},
	methods:{
		...mapMutations(['addShopItem']),
		addCartList(){
			this.addShopItem(this.good);
			console.log('添加到购物车成功')
		}
	}
}
